.research {
  display: none;

  @media (min-width: $tablet-width) {
    display: block;
    position: relative;
    z-index: 1;
    padding-top: 95px;
    padding-bottom: 43px;
    padding-right: 20px;
    padding-left: 20px;
    background-color: $white;
    color: $dark-blue-desaturarion;
  }

  @media (min-width: $desktop-width) {
    display: block;
    position: relative;
    z-index: 1;
    padding-top: 111px;
    padding-bottom: 112px;
    padding-right: 65px;
    padding-left: 65px;
    background-color: $grey;
  }
}

.research__center {
  position: relative;
  margin: 0 auto;
  width: 620px;

  @media (min-width: $tablet-width) {
    &::before {
      content: "";
      position: absolute;
      top: -41px;
      left: 0;
      width: 238px;
      height: 276px;
      background-image: url("../img/page-content/research/timer.svg");
      background-size: contain;
      background-position: center center;
    }
  }

  @media (min-width: $desktop-width) {
    width: 940px;

    &::before {
      top: -94px;
      left: 0;
      width: 298px;
      height: 345px;
    }
  }
}

.research__wrapper {
  margin-left: auto;
  width: 310px;

  @media (min-width: $desktop-width) {
    width: 570px;
  }
}

.research__lead {
  display: block;
  margin: 0;
  margin-bottom: 11px;
  width: 170px;
  color: $black;
  font-size: 30px;
  line-height: 36px;

  @media (min-width: $desktop-width) {
    margin-bottom: 26px;
    width: 100%;
    line-height: 30px;
  }
}

.research__text {
  margin: 0;
  margin-bottom: 10px;

  @media (min-width: $desktop-width) {
    margin-bottom: 30px;
  }
}

.research__link {
  color: $pink;
}
